import React from 'react';
import clsx from 'clsx';
import { ChevronRightIcon } from '@ui/icons/material/ChevronRight';
export function BreadcrumbItem(props) {
  const {
    isCurrent,
    sizeStyle,
    isMenuTrigger,
    isClickable,
    isDisabled,
    onSelected,
    className,
    isMenuItem,
    isLink
  } = props;
  const children = typeof props.children === 'function' ? props.children({
    isMenuItem
  }) : props.children;
  if (isMenuItem) {
    return children;
  }
  const domProps = isMenuTrigger ? {} : {
    tabIndex: isLink && !isDisabled ? 0 : undefined,
    role: isLink ? 'link' : undefined,
    'aria-disabled': isLink ? isDisabled : undefined,
    'aria-current': isCurrent && isLink ? 'page' : undefined,
    onClick: () => onSelected?.()
  };
  return <li className={clsx(`relative inline-flex min-w-0 flex-shrink-0 items-center justify-start ${sizeStyle?.font}`, (!isClickable || isDisabled) && 'pointer-events-none', !isCurrent && isDisabled && 'text-disabled')}>
      <div {...domProps} className={clsx(className, 'cursor-pointer overflow-hidden whitespace-nowrap rounded px-8', !isMenuTrigger && 'py-4 hover:bg-hover', !isMenuTrigger && isLink && 'outline-none focus-visible:ring')}>
        {children}
      </div>
      {isCurrent === false && <ChevronRightIcon size={sizeStyle?.icon} className={clsx(isDisabled ? 'text-disabled' : 'text-muted')} />}
    </li>;
}